    <template>
        <div class="container">
            <div class="player">
                <video-player class="video-player vjs-custom-skin"
                              ref="videoPlayer"
                              :playsinline="true"
                              :options="playerOptions"
                              @play="onPlayerPlay($event)"
                              @pause="onPlayerPause($event)"
                >
                </video-player>
            </div>
        </div>
    </template>

    <script>
        import { videoPlayer } from 'vue-video-player';
        export default {
            data () {
                return {
                    playerOptions: {
    //  playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
                        autoplay: false, //如果true,浏览器准备好时开始回放。
                        muted: false, // 默认情况下将会消除任何音频。
                        loop: false, // 导致视频一结束就重新开始。
                        preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
                        language: 'zh-CN',
                        aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
                        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
                        sources: [{
                            withCredentials: false,
                            type: "video/mp4",
                            src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
    //                        type: "application/x-mpegURL",
    //                        src: "https://logos-channel.scaleengine.net/logos-channel/live/biblescreen-ad-free/playlist.m3u8"
                        }],
                        flash: { hls: { withCredentials: false }},
                        html5: { hls: { withCredentials: false }},
                        poster: "poster.jpg", //你的封面地址
                        width: document.documentElement.clientWidth,
                        notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
    //  controlBar: {
    //   timeDivider: true,
    //   durationDisplay: true,
    //   remainingTimeDisplay: false,
    //   fullscreenToggle: true //全屏按钮
    //  }
                    }
                }
            },
            components: {
                videoPlayer
            },
            methods: {
                onPlayerPlay(player) {
    //                alert("play");

                },
                onPlayerPause(player){
    //                alert("pause");
                },
            },
            mounted: function () {
                let player= window.player=this.$refs.videoPlayer.player;

                player.controlBar.removeChild("volumePanel")
                player.controlBar.removeChild("fullscreenToggle")
                player.controlBar.removeChild("durationDisplay")
                player.controlBar.removeChild("progressControl")

                this.$currentTimeDisplay= player.controlBar.getChild("currentTimeDisplay");
                this.$progressControl= player.controlBar.addChild("progressControl")
                this.$durationDisplay=player.controlBar.addChild("durationDisplay");
                this.$fullscreenToggle=player.controlBar.addChild("fullscreenToggle")
                let container= document.querySelector(".video-js"),
                    userPanel= document.createElement("div")
                userPanel.setAttribute("class","user-panel")
                container.appendChild(userPanel);

                player.on("useractive",function(e){console.log(e)})
            },
            computed: {
                player() {
                    window.player=this.$refs.videoPlayer.player
                    return this.$refs.videoPlayer.player
                }
            }
        }
    </script>

